                  на детском празднике показывают детям фокусы, в которых они
                  сами могут принять участие и попробовать разгадать их секрет.
                  Конечно же, мы не забываем про традиционную песню "Каравай".
                  Помимо раздачи уже заготовленных заранее подарков, наши
                  аниматоры дарят всем детям фигурки из воздушных шариков.
                </p>

                <h3>Сколько по времени длится детский праздник?</h3>
