иняв нужные образы, начинают свой праздник под наше музыкальное сопровождение со знакомства и приветствия. Детям необходимо минимальное время для адаптации, малышам его потребуется чуть больше. Мы не забываем про танцевальные конкурсы, потому что в танце каждый ребенок может проявить свою индивидуальность. Помимо веселых, необычных и увлекательных конкурсов с многочисленным реквизитом, аниматоры на детском празднике показывают детям фокусы, в которых они сами могут принять участие и попробовать разгадать их секрет. Конечно же, мы не забываем про традиционную песню \"Каравай\". Помимо раздачи уже заготовленных заранее подарков, наши аниматоры дарят всем детям фигурки из воздушных шариков. ")]),_c('h3',[_vm._v("Сколько по времени длится детский праздник?")]),_c('p',[_vm._v(" Стандартный праздник длится один час. Это не значит, что 1 час и ни минутой больше. Ни в коем случае! По Вашему усмотрению и по настроению детей, праздник может идти и 2, и 3 и более часов. Для самых маленьких достаточно будет стандартной одночасовой программы, т.к. малыши быстро устают. Для детей постарше праздник соответственно может быть дольше, особенно если он проходит на свежем воздухе или по принципу \"Квеста\". ")])]),_c('div',{staticClass:"col-md-6"},[_c('h3',[_vm._v("Каких персонажей пригласить на детский праздник?")]),_c('p',[_vm._v(" Частый вопрос: сколько аниматоров позвать на детский праздник? Справится ли один? Конечно, справится. Но интереснее проходит праздник, когда аниматоров двое и больше по причине того, что сюжетная линия героев больше просматривается и к тому же можно устроить небольшие соревнования. В любом случае, выбирать только Вам. ")]),_c('h3',[_vm._v("Что такое творческая мастерская?")]),_c('p',[_vm._v(" Наша творческая мастерская полна интересных и увлекательных занятий для детей. Для девчонок интересен будет мастер-класс по декорированию сумок. Только представьте, Ваш ребенок сможет сам сделать себе сумку и забрать её с собой в качестве сувенира с праздника. Не будем забывать и про мальчишек. Им будет интересно (так же, как и девчонкам), создание оригами из бумаги, а также фигурок из гипса. С детства необходимо развивать творческую жтлку у детишек. Мы стараемся поспособствовать этому самую малость! ")]),_c('h3',[_vm._v("Где лучше всего провести праздник?")]),_c('p',[_vm._v(" Прежде чем заказать детский праздник, необходимо позаботиться в каком месте это будет проходить. Это может быть организовано дома, в кафе, даже на открытом воздухе. Самое важно, что необходимо при этом учитывать - это наличие свободного места, чтобы дети вместе с аниматорами смогли повеселиться в полной мере. Соответственно, чем больше количество детей на празднике, тем больше должно быть места. Не стоит забывать и про наличие розетки или генератора в выбранном месте. Иначе весь праздник пройдет в тишине, что тоже безусловно может сказаться на атмосфере мероприятия. Выбирайте такое место, где будет комфортно Вам и Вашим детям, где можно будет с легкостью немного пошуметь! ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-12 text-center"},[_c('div',{staticClass:"section-heading"},[_c('h2',{staticClass:"title",staticStyle:{"margin-bottom":"30px","color":"#fff"}},[_vm._v(" Фото ")])])])])}]

